import React from 'react';
import {render, fireEvent, screen} from '@testing-library/react-native';
import ContactItem from '../ContactItem';

describe('ContactItem', () => {
  const contact = {
    firstName: 'John',
    lastName: 'Doe',
    photo: 'https://example.com/avatar.png',
    age: 2,
    id: '111',
  };

  afterEach(() => {
    jest.clearAllMocks();
  });

  test('renders correctly with contact data', () => {
    const {getByText, getByTestId} = render(<ContactItem contact={contact} />);

    const avatar = getByTestId('avatar-image');
    const name = getByText('John Doe');

    expect(avatar.props.firstName).toBe('John');
    expect(avatar.props.lastName).toBe('Doe');
    expect(avatar.props.photo).toBe('https://example.com/avatar.png');
    expect(name).toBeTruthy();
  });

  test('calls onPress when pressed', () => {
    const onPressMock = jest.fn();
    const {getByTestId} = render(
      <ContactItem contact={contact} onPress={onPressMock} />,
    );

    const touchable = getByTestId('contact-item-touchable');
    fireEvent.press(touchable);

    expect(onPressMock).toHaveBeenCalled();
  });
});
